@value cardBackground, borders from "../../colors.module.css";

.wrapper{
    font-size: 12px;
    height: 150px;
    width: 60px;
    border: solid 2px borders;
    border-radius: 5px;
    background-color: cardBackground;
    padding: 10px 5px 10px;
    margin: 2px 10px 2px 0;
}

.wrapper2{
    margin: 20px 0px 50px 95px;
    display: flex;
    flex-direction: row;
    height: 92px;
}

.slider{
    height: 47px;
    margin-bottom: -40px;
}

.rating{
    width: 20px;
    transform: scale(1.2) rotate(-90deg);
    margin-bottom: -70px;
    margin-right: -90px;
}