@value cardBackground, borders from "../../colors.module.css";

.wrapper{
    height: 150px;
    width: 300px;
    border: solid 2px borders;
    border-radius: 5px;
    background-color: cardBackground;
    padding: 10px 15px 10px;
    margin: 2px;
}

.select {
    width: 100%;
    height: 100px;
    margin: 0;
}

.option{
    font-family: 'Montserrat';
    font-size: 14px;
}

.textField{
    width: 180px;
    height: 27px;
    border-radius: 5px;
    padding: 2px 10px;
    margin: 0px;
    font-size: 14px;
}

.clearButton {
    composes: button from "../../Styles/buttons.module.css";
    margin: 5px 0px 5px 15px;
    width: 80px;
    height: 32px;
}