@value cardBackground, borders from "../../colors.module.css";

.wrapping {
    max-width: 72%;
    font-size: 12px;
    background-color: cardBackground;
    border: solid 2px borders;
    border-radius: 5px;
    margin: 2px 10px 2px 0;
    padding: 10px;
    display: flex;
    flex-direction: column;
    height: auto;
    align-items: center;
}

.wrapping[data-slimview='true']{
    flex-direction: row;
}

.dropdownWrap{
    margin: 0px 10px;
}

.dropdown {
    font-size: 12px !important;
    font-family: 'Montserrat' !important;
    margin: 5px 0px 5px !important;
}
.checkboxWrap {
    font-size: 12px;
    margin: -5px 0px;
}
.checkboxWrap * {
    padding: 3px !important;
}