@value cardBackground, borders from "../../colors.module.css";

.wrapper {
    width: 100%;
    max-width: 1580px;
    margin: auto;
    /* height: 50px; */
    border-bottom: solid 2px borders;
}

.logo{
    font-weight: bold;
    font-size: 30px;
    color: rgb(85,85,85);
    padding: 10px 10px;
    margin: auto 20px;
}

.spacer {
    flex-grow: 1;
}

.inside {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: flex-end;
    padding: 10px;
}

.filterButton{
    composes: button from "../../Styles/buttons.module.css";
    background-color: cardBackground;
    border-radius: 5px;
    margin: 10px 0px;
    height: 50px;
    width: 50px;
    padding: 13px;
}